.g-lig-remains {
  position: absolute;
}
.g-lig-remains > .remains-main {
  height: 100%;
  width: 100%;
  animation: switchover 1500ms linear;
}
@keyframes switchover {
  0% {
    background: url('../../../../assets/images/games/lightning/explosion/disp_01.png') no-repeat center / 100% 100%;
  }
  20% {
    background: url('../../../../assets/images/games/lightning/explosion/disp_02.png') no-repeat center / 100% 100%;
  }
  40% {
    background: url('../../../../assets/images/games/lightning/explosion/disp_03.png') no-repeat center / 100% 100%;
  }
  60% {
    background: url('../../../../assets/images/games/lightning/explosion/disp_04.png') no-repeat center / 100% 100%;
  }
  80% {
    background: url('../../../../assets/images/games/lightning/explosion/disp_05.png') no-repeat center / 100% 100%;
  }
  95% {
    background: url('../../../../assets/images/games/lightning/explosion/disp_06.png') no-repeat center / 100% 100%;
  }
  100% {
    background: url('../../../../assets/images/games/lightning/explosion/disp_06.png') no-repeat center / 100% 100%;
    opacity: 0.5;
  }
}
