.g-tic-scoreboard {
  display: flex;
  padding: 0.42rem 0.32rem 0;
  justify-content: space-between;
  align-items: center;
}
.g-tic-scoreboard .player {
  font-family: "TheHistoriaDemo";
  color: #511800;
  font-size: 0.35rem;
}
.g-tic-scoreboard .player i {
  display: block;
  width: 0.2rem;
  height: 0.2rem;
  margin: 0.05rem auto;
}
.g-tic-scoreboard .player .playerA {
  background: url('../../../../assets/images/games/tictactoe/x.png') no-repeat center;
  background-size: 100% 100%;
}
.g-tic-scoreboard .player .playerB {
  background: url('../../../../assets/images/games/tictactoe/o.png') no-repeat center;
  background-size: 100% 100%;
}
.g-tic-scoreboard .score {
  font-size: 0.25rem;
}
