.g-fic-header {
  height: 1.8rem;
  background: url('../../../../assets/images/games/fivechess/header.jpg') no-repeat;
  background-size: 100% 100%;
  position: relative;
}
.g-fic-header .maximize {
  position: absolute;
  top: 0.2rem;
  right: 0.3rem;
  z-index: 999;
}
.g-fic-header .player {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 0.388rem;
  width: 0.388rem;
  position: absolute;
  right: 0.2rem;
  bottom: 0.2rem;
}
.g-fic-header .player .spark {
  position: absolute;
  height: 100%;
  width: 100%;
  background: url('../../../../assets/images/games/fivechess/spark.png') no-repeat;
  background-size: 100% 100%;
  animation: rotate 1s linear infinite;
}
.g-fic-header .player .piece {
  height: 0.28rem;
  width: 0.28rem;
}
.g-fic-header .player .pieceBlack {
  background: url('../../../../assets/images/games/fivechess/playerBlack.png') no-repeat;
  background-size: 100% 100%;
}
.g-fic-header .player .pieceWhite {
  background: url('../../../../assets/images/games/fivechess/playerWhite.png') no-repeat;
  background-size: 100% 100%;
}
@keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}
