@font-face {
  font-family: "game";
  src: url('../../../../assets/font/game.ttf') format('truetype');
}
.g-lig-equip {
  position: absolute;
  left: 0.1rem;
  bottom: 0.1rem;
  width: 0.7rem;
  height: 1.13rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.g-lig-equip .content {
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.g-lig-equip .content > .icon {
  width: 0.45rem;
  height: 0.45rem;
}
.g-lig-equip .content > .life {
  background: url('../../../../assets/images/games/lightning/equip/life_count.png') no-repeat center / 100% 100%;
}
.g-lig-equip .content > .bomb {
  background: url('../../../../assets/images/games/lightning/equip/bomb_count.png') no-repeat center / 100% 100%;
}
.g-lig-equip .content > .count {
  color: #ff3314;
  font-size: 0.3rem;
  font-family: game;
  background-image: -webkit-linear-gradient(bottom, #f2c269 0%, #ff4e24 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 800;
}
