.g-tic-victory {
  font-size: 0.34rem;
  color: red;
  position: absolute;
  width: 60%;
  height: 1.3rem;
  text-align: center;
  top: 50%;
  left: 50%;
  margin-left: -30%;
  margin-top: -50%;
  font-family: 'TheHistoriaDemo';
}
.g-tic-victory .winner {
  background-image: -webkit-linear-gradient(bottom, #fff5d5 0%, #c5a572 10%, #fffde4 25%, #a5703a 50%, #fffde4 75%, #c5a572 95%, #fff5d5 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.g-tic-victory .again {
  color: #e94f4f;
}
