.home-TabItem {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding: 0.15rem 0.2rem;
  background: white;
  margin-bottom: 0.08rem;
}
.home-TabItem .TabItem-banner {
  width: 0.62rem;
  height: 0.62rem;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  border-radius: 0.08rem;
}
.home-TabItem .TabItem-content {
  width: 2rem;
}
.home-TabItem .TabItem-content > h5 {
  margin: 0;
  line-height: 0.25rem;
  font-size: 0.16rem;
}
.home-TabItem .TabItem-content > p {
  margin: 0;
  line-height: 0.19rem;
  font-size: 0.12rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
.home-TabItem .TabItem-button {
  width: 0.54rem;
  height: 0.26rem;
  border: 1px solid #108ee9;
  line-height: 0.24rem;
  text-align: center;
  color: #108ee9;
  border-radius: 0.02rem;
}
