.g-tic-game {
  height: 100%;
  background: url('../../../assets//images/games/tictactoe/gameBg.jpg') no-repeat;
  background-size: 100% 100%;
}
.g-tic-game ol,
.g-tic-game ul {
  padding-left: 30px;
}
.g-tic-game .status {
  margin-bottom: 10px;
}
.g-tic-game .g-tic-game {
  display: flex;
  flex-direction: row;
}
.g-tic-game .g-tic-info {
  margin-top: 0.5rem;
  display: flex;
  justify-content: space-around;
}
.g-tic-game .g-tic-info .operate {
  font-family: "TheHistoriaDemo";
  color: #511800;
  font-size: 90px;
}
.g-tic-game .fontH {
  font-family: "TheHistoriaDemo";
  color: #511800;
  font-size: 40px;
}
