.g-fic-square {
  background: transparent;
  box-sizing: border-box;
  font-weight: bold;
  height: 100%;
  padding: 0;
  text-align: center;
  width: 100%;
}
.g-fic-square .squareItem {
  height: 100%;
}
.g-fic-square .squareItem .squarePiece {
  height: 100%;
}
.g-fic-square .squareItem .squareBlack {
  background: url('../../../../assets/images/games/fivechess/black.png') no-repeat center;
  background-size: 90% 90%;
}
.g-fic-square .squareItem .squareWhite {
  background: url('../../../../assets/images/games/fivechess/white.png') no-repeat center;
  background-size: 90% 90%;
}
.g-fic-square .squareWin {
  border-radius: 50%;
  background: radial-gradient(rgba(5, 246, 253, 0) 0%, #05f6fd 90%, rgba(255, 255, 255, 0) 100%);
}
