.g-lig-engine {
  height: 100%;
  position: relative;
  overflow: hidden;
}
.g-lig-engine > .background {
  height: 200%;
  width: 100%;
  position: relative;
  position: absolute;
  top: -100%;
  animation: downward 15s linear infinite;
  background: url('../../../../assets/images/games/lightning/bg2.jpg') repeat-y;
  background-size: 100% 50%;
}
.g-lig-engine .pause {
  height: 43Px;
  background: red;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 20;
}
.g-lig-engine .pauseT {
  height: 43Px;
  background: red;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 20;
}
@keyframes downward {
  to {
    top: 0%;
  }
}
