.g-lig-Pause .Pause-main {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 100;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgba(255, 255, 255, 0.7);
}
.g-lig-Pause .start:before {
  font-size: 100px;
}
.g-lig-Pause .Pause-pause {
  position: absolute;
  right: 15PX;
  top: 15Px;
  z-index: 100;
  color: rgba(255, 255, 255, 0.6);
}
.g-lig-Pause .Pause-pause:before {
  display: block;
  font-size: 50px;
}
