.g-tic-pointer {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.g-tic-pointer .pointer {
  height: 0.13rem;
  width: 90%;
  background: url('../../../../assets//images//games/tictactoe/result.png') no-repeat center;
  background-size: 100% 100%;
}
.g-tic-pointer .crosswise0 {
  position: relative;
  top: -34%;
}
.g-tic-pointer .crosswise2 {
  position: relative;
  top: 32%;
}
.g-tic-pointer .vertical {
  transform: rotate(90deg);
}
.g-tic-pointer .vertical0 {
  position: relative;
  left: -34%;
}
.g-tic-pointer .vertical2 {
  position: relative;
  left: 33%;
}
.g-tic-pointer .slant {
  width: 100%;
}
.g-tic-pointer .slant0 {
  transform: rotate(45deg);
}
.g-tic-pointer .slant1 {
  transform: rotate(-45deg);
}
